<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row>
          <el-col>
            <el-form-item label="产品名">
              <el-input v-model="searchs.keyword_search_value" placeholder="姓名/手机号"></el-input>
            </el-form-item>
            <el-form-item label="产品名">
              <el-input v-model="searchs.jp_name" placeholder="奖品名"></el-input>
            </el-form-item>
            <el-form-item label="是否显示在中奖列表">
              <el-select size="small" v-model="searchs.is_show_index" clearable placeholder="请选择">
                <el-option value="Y" label="是" />
                <el-option value="N" label="否" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
              <router-link to="/prize/create">
                <el-button style="margin-left: 8px;" type="success">创建</el-button>
              </router-link>
              <el-button style="margin-left: 8px;" type="primary" @click="setShows('Y')">批量显示中奖</el-button>
              <el-button style="margin-left: 8px;" type="warning" @click="setShows('N')">批量关闭中奖</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading" @selection-change="handleSelectionChange">
      >
      <el-table-column type="selection" width="55" />
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" />
      <el-table-column prop="jp_name" label="奖品" align="center" />
      <el-table-column prop="jp_pro" label="奖品概率" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="is_show_index_name" label="是否显示在中奖列表" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" width="200" />
      <el-table-column prop="address" align="center" label="操作" width="200px">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="eidt(scope.row.id)">编辑</el-button>
          <el-button type="danger" size="small" @click="remove(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'
import Fecth4 from '../../utils/fetch4'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        keyword_search_value: '',
        status: '',
        is_show_index: '',
        jq_name: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        status: null,
      },
      selectIds: [],
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    eidt(id) {
      this.$router.push('/prize/edit/' + id)
    },
    remove(id) {
      Fecth4('/user/prize/delete/' + id).then(() => {
        this.FetchList()
      })
    },
    FetchList() {
      this.loading = true
      Fecth('/user/prize/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    handleSelectionChange(val) {
      this.selectIds = []
      val.forEach(item => {
        this.selectIds.push(item.id)
      })
    },
    setShows(type) {
      if (this.selectIds.length < 1) {
        Message.error('未选中数据')
        return
      }

      var title = '此操作将显示在前台展示, 是否继续?'
      if (type === 'N') {
        title = '此操作将不显示在前台展示, 是否继续?'
      }
      this.$confirm(title, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.loading = true
          Fecth('/user/prize/setShows', { ids: this.selectIds, type: type })
            .then(() => {
              this.loading = false
              Message.success('设置成功')
              this.FetchList()
            })
            .catch(() => (this.loading = false))
        })
        .catch(() => {})
    },
  },
}
</script>
